* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  user-drag: none;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-drag: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  perspective: 800px;
}

#root {
  display: flex;
  align-items: center;
  justify-content: center;
  background: #000;
  overflow: hidden;
}

#container {
  display: flex;
  flex-direction: column;
  width: 400px;
  height: 100vh;
  align-items: center;
  justify-content: center;
  transition: 0.2s;
}

#app {
  width: 400px;
  height: 410px;
  padding-top: 119px;
  display: flex;
  align-items: center;
  justify-content: center;
}

#card {
  position: absolute;
  width: 320px;
  height: 201px;
  border-radius: 10px;
  perspective: 800px;
  transform: rotate3d(1, 0, 0, 60deg);
  background-repeat: no-repeat;
  background-size: cover;
  color: #fff;
}

#num {
  color: #fff;
  font-size: 17px;
  font-weight: 100;
  letter-spacing: 1px;
  font-family: monospace;
}

#expiry {
  font-size: 13px;
  color: #fff;
  font-weight: 100;
  letter-spacing: 1px;
  font-family: monospace;
}

#label {
  color: #fff;
  font-size: 7px;
  font-weight: 100;
  letter-spacing: 1px;
  font-family: monospace;
}

#cardContent {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  position: absolute;
  height: 100%;
  width: 100%;
  box-shadow: inset 0 0 1px 1px #00000040;
  padding: 16px;
  border-radius: 10px;
}

#details {
  position: relative;
  margin-bottom: 15px;
  font-size: 28px;
  color: #fff;
}

#detail {
  font-family: Roboto-Regular, HelveticaNeue, Arial, sans-serif;
  width: 200px;
  position: relative;
  font-size: 24px;
  /* display: flex; */
  flex: 1;
  float: left;
  padding-left: 42px;
  padding-top: 12px;
  font-weight: bold;
  letter-spacing: 2px;
}

#infolabel {
  font-weight: 100;
  font-size: 12px;
  color: #8b8b8b;
  letter-spacing: 1px;
  flex: 1;
}

#title {
  font-family: Roboto-Regular, HelveticaNeue, Arial, sans-serif;
  position: relative;
  font-size: 20px;
  margin-bottom: 20px;
  color: rgb(194, 194, 194);
  font-weight: bold;
}

#exp {
  display: flex;
}
